import PageButton from '../../components/button.vue';

export default {
	components: {

		PageButton
	},
	data() {
		return {
			temp: {
				inquiryId: '',
				geneId: ''
			},
			geneList: [{"geneId":"","geneName":"","genePrice":"","geneDetail":""}],
			index: null,
		}
	},
	onLoad(options) {
		let that = this;
		that.temp.inquiryId = options.inquiryId;
		that.getGeneList();
	},
	methods: {
		bindPickerChange: function(e) {
			let that = this;
			that.index = e.target.value
			that.temp.geneId = that.geneList[that.index].geneId
		},
		getGeneList:function(){
			let that = this;
			that.$util.request({
				url: "/didi-doctor/geneinfo/findList",
				param:{states:"1"},
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					that.geneList = res.data;
				},
				error: function() {}
			})
		},
		submit: function() {
			let that = this;
			if (!that.$util.isEmpty(that.temp.geneId)) {
				uni.showToast({
					title: '请选择检测种类',
					icon: 'none',
					duration: 2000
				});
				return;
			}
			that.$util.request({
				url: "/didi-doctor/inquirygeneinfo/add",
				param: that.temp,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					uni.showToast({
						title: '新增成功',
						icon: 'success',
						duration: 2000
					})
					setTimeout(function() {
						uni.navigateBack({
							delta: 1
						})
					}, 500)
				},
				error: function() {}
			})
		}
	}
}
